import Vue from 'vue'
import axios from 'axios'
import router from '../../router'
import store from '../../store1';
import { Message } from 'iview';
Vue.prototype.$Message = Message
// import store from '@/store'
//axios的一些配置，比如发送请求显示loading，请求回来loading消失之类的
//
axios.interceptors.request.use(function(config) { //配置发送请求的信息
    // config.data = encryption(JSON.stringify(config.data));
    if (typeof config.data == 'object') {
        var data = new Array();
        for (var key in config.data) {
            data.push(key + '=' + config.data[key]);
        }
        config.data = data.join("&");
    }
    return config;
}, function(error) {
    return Promise.reject(error);
});

axios.interceptors.response.use(function(response) { //配置请求回来的信息
    // response.data.data =JSON.parse(decrypt(response.data.data))
    // if(response.data.data.code == 0){
    //   Vue.$router.push({path: '/login'});
    //   return;
    // }
    if (response.data.status == 1001) {
        Vue.prototype.$Message.warning(response.data.ret_msg);
    }
    if (response.data.status === 1202) {
        //token失效
        // localStorage.setItem('token','');
        localStorage.removeItem('token')
        var Obj = {}
        store.dispatch('SET_USER_INFO_DATA', Obj)
        router.push({
            name:"Home",
            path:'/home',
            params: {timeOut: true}
        });
        return false;
    }
    return response;
}, function(error) {

    return Promise.reject(error);
});
// axios.defaults.baseURL = 'http://59.148.200.202/';
let hostname = window.location.hostname
if(hostname.indexOf('localhost')!=-1||hostname.indexOf('192')!=-1){
    // Vue.prototype.urlRequest = '/index.php'
    Vue.prototype.urlRequest = 'https://www.appbale.net'
    
}else{
    Vue.prototype.urlRequest = '/'    
}
axios.defaults.baseURL = Vue.prototype.urlRequest;

