import * as types from './types'
import getters from './getters'
import state from './state'

// const state = {
// 	count: 20
// };

const mutations = {
    //修改个人信息
    [types.HEADER_SHOW](state) {
        state.headerShow = true;
    },
    [types.FOOTER_HIDE](state) {
        state.footerShow = false;
    },
    [types.HEADER_HIDE](state) {
        state.headerShow = false;
    },
   
    [types.SET_BANKED_DATA](state, obj) {
        state.bankedData = obj;
    },
    [types.SET_ISLOGIN](state, boolean) {
        state.bankedData = boolean;
    },
    [types.SET_HEADER_FOOTER](state,obj){
        state.getHeaderFooter = obj
    },
    [types.SET_PLATFORM_CONFIG](state,obj){
        state.platformConfig = obj
    },
    [types.SET_USER_DATA](state,obj){
        state.userData = obj
    },
    isUser(state,val){
        state.nickname = val
    },
  
    money_freeze(state,val){
        state.money_freeze = val
    },
    setStopBet(state,val){
        state.stopBet = val
    },
    SET_SERVICE(state,String){
        state.service = String
    },

    SET_USER_INFO_DATA(state,Obj){
        state.userInfo = Obj
    },
    SET_ACCOUNT_DATA(state,Obj){
        state.accountData = Obj
    },
    setAccountData(state,Obj){
        state.accountData = Obj
    },
    setShowMiPai(state,Obj){
        state.showMiPai = Obj
    },
    setShowAskRoad(state,Obj){
        state.showAskRoad = Obj
    },
    //平台公告ID
    noticeId(state,id){
        state.id = id
    },
    setQdObj(state,Obj){
        state.qdObj = Obj
    },
    setSerTime(state,Obj){
        state.serTime = Obj
    },
    setLotRstData(state,Obj){
        state.lotRstData = Obj
    },
    setNewRstData(state,Obj){
        state.newRstData = Obj
    },

};
export default {
    state,
    mutations,
    getters
}


