<template>
    <div :class="{'is-fixed': isFixedClass}">
      <!-- header -->
      <header>
        <div class="top-content clearfix">
          <div class="w-1200 clearfix">

            <div class="right-cont fr" :class="!userInfo.username ? 'notLog' : userInfo.username ? 'logged' : ''">
            <!-- <div class="right-cont fr notLog"> -->
              <!-- 未登录 -->
              <div class="logInfo" v-if="!userInfo.username">
                <p class="wel-tips">{{english.您好}}，{{english.欢迎来到}} {{platformConfig.platform_name}}！</p>
                <a href="javascript:void(0);" class="top-btn btn-red" @click="login">{{english.登录}}</a>
                <!-- <a href="javascript:void(0);" class="top-btn btn-blue" @click="register">{{english.注册}}</a> -->
                <em>|</em>
                <a :href="service == '暂无' ? 'javascript:void(0);' : service" target="_blank">{{english.客服中心}}</a>
              </div>
              <!-- 已登录 -->
              <div class="logInfo" v-if="userInfo.username">
                <a href="javascript:void(0);" @mouseover="showQd=true" @mouseout="showQd=false" v-if="closeQd"><img src="@/assets/images/qd.gif" alt="" class="head-qd"> {{getQdObj.qdState==0?english.每日签到:english.已签到}}
                  <div class="qdPanel" v-show="showQd">
                    <div>
                      <Steps :current="getQdObj.qdCurrent">
                        <Step :content="qdMoney+english.元宝"></Step>
                        <Step :content="qdMoney+english.元宝"></Step>
                        <Step :content="qdMoney+english.元宝"></Step>
                        <Step :content="qdMoney+english.元宝"></Step>
                        <Step :content="qdMoney+english.元宝"></Step>  
                        <Step :content="qdMoney+english.元宝"></Step>
                        <Step :content="moneyBal+english.元宝"></Step>
                      </Steps>
                      <div class="qdCtr">
                        <p v-html="qdText"></p>
                        <Button type="error" @click="setQd" :loading="qdLoading" v-if="getQdObj.qdState==0">{{english.签到}}</Button>
                        <Button type="error" v-else>{{english.已签到}}</Button>
                      </div>
                    </div>
                  </div>
                </a>
                <em v-if="closeQd">|</em>
                <a :href="service == '暂无' ? 'javascript:void(0);' : service" target="_blank">{{english.客服中心}}</a>
                <em>|</em>
                <router-link to="/leaderBoard"><img src="@/assets/images/rankinglist.png" alt="" style="vertical-align: middle;"> {{english.英雄榜}}</router-link>
                <em>|</em>
                <router-link :to="{path: '/personalCenter/announcement', query: {query:Date.parse(new Date())}}"><i  class="icon img-mes"><em v-if="userInfo.un_read_msg" class="red-dot"></em></i>{{english.平台公告}}</router-link>
                <em>|</em>
                <router-link :to="{path: '/personalCenter/personalInfo', query: {query:Date.parse(new Date())}}"><i class="icon img-person"></i>{{userInfo.username}}</router-link>
                <span style="margin-left:10px;">{{english.余额}}: <i class="c-orange">{{accountData?accountData.money_usable:0.00}}</i></span>
                <a href="javascript:void(0)" @click="isRecharge" class="top-btn btn-red">{{english.充值}}</a>
                <a href="javascript:void(0)" @click="isWithdraw" class="top-btn btn-blue">{{english.提现}}</a>
                <em>|</em>
                <a href="javascript:void(0);" @click="exitLogin">{{english.退出}}</a>
              </div>
            </div>
          </div>
        </div>
      </header>
      <nav class="box clearfix">
        <div class="w-1200 clearfix">
          <div class="left-cont clearfix fl">
            <div @click="skipHome" class="logo-box"><img v-if="platformConfig && platformConfig.pc_logo" :src="imgRequest + platformConfig.pc_logo" alt=""></div>
          </div>
          <div class="right-cont fr">
            <ul class="nav-list">
              <li @mouseout="myFor" @mouseover="myForShow" :class="{listHover:showDrop}">
                <router-link to="/home">{{english.购彩大厅}}<span class="icon white-arrow"></span></router-link>
                <transition name="fadeUp">
                <div class="lobbyBox" v-show="myFlag" @mouseout="myFlag=false" @mouseover="myFlag=true">
                  <div class="lobby">
                    <Row type="flex" justify="start">
                      <div v-for="(d,index) in lottery_list" :key="index" class="nav-item">
                        <h2><img :src="imgRequest + d.pic_url_pc_logo" alt=""> {{english[d.name]}}</h2>
                        <div class="link-item">
                          <span v-for="(b,index1) in d.roomList" :key="index1">
                            <a href="javascript:void(0);" @click="goRoom(d.pic_url_pc_logo,d.roomList[index1])" :title="english[b.title]">{{english[b.title]}}</a>
                          </span>
                        </div>
                      </div>
                    </Row>
                  </div>
                </div>
                </transition>
              </li>
              <li><a href="javascript:void(0);" @click="goZouShi">{{english.开奖结果}}</a></li>
              <li><router-link to="/activityCenter">{{english.活动中心}}</router-link></li>
              <li><router-link to="/taskCenter">{{english.任务中心}}</router-link></li>
              <li><router-link to="/downLoad">{{english.手机}}APP</router-link></li>
              <li v-if="userInfo.username"><router-link :to="{path: '/personalCenter/personalInfo', query: {query:Date.parse(new Date())}}"><span class="icon user-icon"></span>{{english.用户中心}}</router-link></li>
            </ul>
          </div>
        </div>   
        
      </nav>     
      <!-- nav -->
      <!-- 注册弹窗 -->
      <Modal :title="english.注册" :scrollable="true" v-model="registerShow" :mask-closable="false" width="400" class-name="" @on-cancel="registerCancel">
        <div v-if="loading">
            <Spin fix size="large"></Spin>
        </div>
        <Form v-else ref="registerInfo" :model="registerInfo" :rules="registerrule" :label-width="120" label-position="right">
          <FormItem :label="english.账号" prop="userName">
              <Input :maxlength="15" :placeholder="english.账号占位符" v-model="registerInfo.userName" ref="username"></Input>
          </FormItem>
          <FormItem :label="english.密码" prop="psd">
              <Input :maxlength="15" type="password" :placeholder="english.密码占位符" v-model="registerInfo.psd"></Input>
          </FormItem>
          <FormItem :label="english.确认 +' '+ english.密码" prop="confirmPsd">
              <Input :maxlength="15" type="password" :placeholder="english.确认密码占位符" v-model="registerInfo.confirmPsd" @keyup.enter.native="registerNow('registerInfo')"></Input>
          </FormItem>
          <!-- <FormItem :label="'QQ'+english.号码" prop="qq" v-if="OptionalItems.qq">
            <Input :placeholder="english.QQ占位符" v-model="registerInfo.qq" ref="qq" @keyup.enter.native="registerNow('registerInfo')"></Input>
          </FormItem>
          <FormItem :label="english.微信" prop="weixin" v-if="OptionalItems.weixin">
            <Input :placeholder="english.微信占位符" v-model="registerInfo.weixin" ref="weixin" @keyup.enter.native="registerNow('registerInfo')"></Input>
          </FormItem>
          <FormItem :label="english.手机+' '+english.号码" prop="mobile" v-if=" OptionalItems.mobile">
            <Input :placeholder="english.手机号码占位符" v-model="registerInfo.mobile" ref="mobile" @keyup.enter.native="registerNow('registerInfo')"></Input>
          </FormItem> -->
          <FormItem :label="english.邮箱" prop="email" v-if="OptionalItems.email">
            <Input :placeholder="english.邮箱占位符" v-model="registerInfo.email" ref="email" @keyup.enter.native="registerNow('registerInfo')"></Input>
          </FormItem>
          <FormItem :label="english.验证码" prop="register" v-if="OptionalItems.register" class="registerInfo-code">
            <Input :placeholder="english.验证码占位符" v-model="registerInfo.register" ref="register" @keyup.enter.native="registerNow('registerInfo')"></Input>
            <img :src="codeUrl" class="verificationCode" @click="verificationCode">
          </FormItem>
          <FormItem :label="english.推荐人" prop="referrercode" class="last-item" v-show="false">
            <Input v-model="registerInfo.referrercode" :placeholder="english.推荐人占位符" ref="referrerCode" @keyup.enter.native="registerNow('registerInfo')"></Input>
          </FormItem>
        </Form>
        <div slot="footer">
          <Button type="error" size="large" :loading="btnLoading" long @click="registerNow('registerInfo')">
            <span v-if="!btnLoading">{{english.立即注册}}</span>
            <span v-else>{{english.注册中}}...</span>
          </Button>
          <div class="autolog-box clearfix">
            <p class="text-center" ><a href="javascript:void(0)" @click="jumpToLogin">{{english.已有账号}}？{{english.去登录}}</a></p>
          </div>
          <div class="third-party" style="display:none">
            <div class="t-title">{{english.第三方账号登录}}</div>
            <div class="t-con">
              <a href="javascript:void(0);"  class="icon-qq"></a>
              <a href="javascript:void(0);"  class="icon-wx"></a>
              <a href="javascript:void(0);"  class="icon-xl"></a>
            </div>
          </div>
        </div>
      </Modal>
      <!--注册弹窗 -->
      <!-- 登录弹窗 -->
      <Modal :title='english.登录' class="vertical" :scrollable="true" v-model="loginShow" :mask-closable="false" width="400" class-name="">
        <Form ref="loginInfo" :model="loginInfo" :rules="loginrule" :label-width="80" label-position="left">
          <FormItem :label="english.账号" prop="loginUserName">
              <Input :placeholder="english.请输入+' '+english.账号" v-model="loginInfo.loginUserName" @keyup.enter.native="loginNow('loginInfo')"></Input>
          </FormItem>
          <FormItem :label="english.密码" prop="loginPsd">
              <Input type="password" :placeholder="english.请输入+' '+english.密码" v-model="loginInfo.loginPsd" @keyup.enter.native="loginNow('loginInfo')"></Input>
          </FormItem>
        </Form>
        <div slot="footer">
          <Button type="error" autofocus size="large" :loading="btnLoading" long @click="loginNow('loginInfo')">
            <span v-if="!btnLoading">{{english.登录}}</span>
            <span v-else>{{english.登录中}}...</span>
          </Button>
          <div class="autolog-box clearfix">
  				  <label for="" class="fl"><Checkbox v-model="rememberPsd">{{english.记住}} {{english.密码}}</Checkbox></label>
            <p class="fr"><a :href="service == '暂无' ? 'javascript:void(0);' : service" target="_blank">{{english.忘记}} {{english.密码}}？</a></p>
            <!-- <a href="###" @click="jumpToService">忘记密码</a> <span>|</span> 后续添加在注册前面 -->
  			  </div>
          <div class="third-party" style="display:none">
            <div class="t-title">{{english.第三方账号登录}}</div>
            <div class="t-con">
              <a href="javascript:void(0);"  class="icon-qq"></a>
              <a href="javascript:void(0);"  class="icon-wx"></a>
              <a href="javascript:void(0);"  class="icon-xl"></a>
            </div>
          </div>
        </div>
      </Modal>
      <Modal
        v-model="passwdModel"
        :title="english.请输入+' '+english.房间+' '+english.密码"
        :mask-closable="false"
        @on-ok="PrivacyRoom"        
        @on-cancel="passwdModel = false"
        width="334"
        >
        <Input v-model="passwdModelvalue" type="password" :placeholder="english.请输入+' '+english.房间+' '+english.密码" style="width: 300px" @on-keyup.13="PrivacyRoom"></Input>
      </Modal>
      <div class="shadel" v-show="popUp">
        <!-- popUp -->
        <div class="popUp">
          <div class="popUpTop">
            <h2>
              <span>{{english.平台公告}}</span>
              <span class="cancel" @click="cancel"></span>
            </h2>
          </div>
          <div class="popUpContent">
            <h2 class="h2Title">{{title}}</h2>
            <div class="content">{{content}}</div>
            <p class="addtime">{{addtime}}</p>
          </div>
          <!-- <div class="flexBox">
            <Row type="flex" justify="center" class="code-row-bg">
              <Col span="20" order="4">
                <h2 class="camp title">{{title}}</h2>
                <p class="camp content">{{content}}</p>
                <p class="camp addtime">{{addtime}}</p>
              </Col>
            </Row>
          </div> -->
        </div>
      </div>
    </div>
</template>
<script>
import formatDate from '../../common/js/date.js';
import { jsonToString } from "@/common/js/jsonToString";
import { mapGetters, mapActions } from "vuex";
import loginStore from "@/store/loginStore";
import { fetchJson } from "@/common/js/request";
import router from "@/router";
import english from '@/assets/json/English.json'

//QQ验证
// const validateQQ = (rule, value, callback) => {
// 	if (rule.required && !value) {
// 		callback(new Error("QQ "+english.不能+" "+english.为空));
// 	// } else if (!/^[1-9]{1}[0-9]{4,}$/.test(value)) {
// 	// 	callback(new Error("输入的QQ号码格式错误！"));
// 	} else {
// 		callback();
// 	}
// };
// //微信验证
// const validateWeixin = (rule, value, callback) => {
// 	if (rule.required && !value) {
// 		callback(new Error(english.微信+" "+english.不能+" "+english.为空));
// 	// } else if (!/^[a-zA-Z0-9_-]{3,30}$/.test(value)) {
// 	// 	callback(new Error("输入的微信格式错误！"));
// 	} else {
// 		callback();
// 	}
// };
// //手机验证
// const validateMobile = (rule, value, callback) => {
// 	if (rule.required && !value) {
// 		callback(new Error(english.手机+ " "+english.号码+" "+english.不能+" "+english.为空));
// 	} else if (!/^[0-9]+\d$/.test(value)) {
// 		callback(new Error(english.输入的手机号码格式错误));
// 	} else {
// 		callback();
// 	}
// };
//邮箱验证
const validateEmail = (rule, value, callback) => {
	if (rule.required && !value) {
		callback(new Error(english.邮箱+" "+english.不能+" "+english.为空));
	} else if (!/^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/.test(value)) {
		callback(new Error(english.输入的邮箱格式错误));
	} else {
		callback();
	}
};
//验证码验证
const validateRegister = (rule, value, callback) => {
	if (rule.required && !value) {
		callback(new Error(english.验证码+" "+english.不能+" "+english.为空));
	} else {
		callback();
	}
};
export default {
  props:{
    tipsFunText:{ //公告弹窗
      type: Number,
      default: 0
    }
  },

  data() {
    //账号验证
    const validateUserName = (rule, value, callback) => {
      if (!value) {
        callback(new Error(english.账号+" "+english.不能+" "+english.为空));
      } else if (!/^[0-9a-z]{6,15}$/i.test(value)) {
        callback(new Error(english.账号占位符));
      } else if (value == this.userName) {
        callback(new Error(english.账号已存在));
      } else {
        callback();
      }
    };
    //密码验证
    const validatePsd = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(english.密码+" "+english.不能+" "+english.为空));
      } else if (!/^(?![^a-zA-Z]+$)(?!\D+$).{6,15}$/.test(value)) {
        callback(new Error(english.密码占位符));
      } else {
        if (this.registerInfo.confirmPsd !== "") {
          this.$refs.registerInfo.validateField("confirmPsd");
        }
        callback();
      }
    };
    //确认密码验证
    const validateConfirmPsd = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(english.确认密码占位符));
      } else if (value !== this.registerInfo.psd) {
        
        callback(new Error(english.两次密码输入不一致));
      } else {
        callback();
      }
    };
    //推荐人验证
    const validateReferrercode = (rule, value, callback) => {
      if (value == this.referrer && value !== "") {
        callback(new Error(english.推荐人不存在));
      } else {
        callback();
      }
    };
    //登录验证
    const validateLoginUserName = (rule, value, callback) => {
      if (!value) {
        callback(new Error(english.账号+" "+english.不能+" "+english.为空));
      }
      //  else if (!/^[0-9a-z]{6,15}$/i.test(value)) {
      //   callback(new Error("账号为6-15位字符，必须是字或字母"));
      // } 
      else {
        callback();
      }
    };
    const validateLoginPsd = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(english.密码+" "+english.不能+" "+english.为空));
      } 
      // else if (!/^(?![^a-zA-Z]+$)(?!\D+$).{6,15}$/.test(value)) {
      //   callback(new Error("请输入6-15位且必须包含英文和数字的密码"));
      // } 
      else {
        callback();
      }
    };
    return {
      isFixedClass:false, //导航浮动
      earn_rate:"",
      addtime:"",
      title:"",
      item:[],
      id:"",
      content:"",
      popUp: false,
      withdrawTotal:"",
      myFlag:false,
      lottery_list:[],
      rememberPsd: false, //记住密码
      OptionalItems:{}, //注册选项是否显示
      Optstate:null, //1表示必填 0表示选填
      loading:true, //注册界面加载
      showDrop: false, //菜单是否显示下拉
      //注册信息
      registerInfo: {
        userName: "",
        psd: "",
        confirmPsd: "",
        // qq: "",
        // weixin: "",
        // mobile: "",
        email: "",
        register: "",
        referrercode: ""
      },
      referrer: "",
      registerrule: {
        psd: [{ validator: validatePsd, trigger: "blur", required: true }],
        confirmPsd: [{ validator: validateConfirmPsd, trigger: "blur", required: true }],
        userName: [{ validator: validateUserName, trigger: "blur", required: true }],
        referrercode: [{ validator: validateReferrercode, trigger: "blur" }]
      },
      //登录信息
      loginInfo: {
        loginUserName: localStorage.getItem('userName'),
        loginPsd: localStorage.getItem('password'),
      },
      loginrule: {
        loginUserName: [{ validator: validateLoginUserName, trigger: "blur" }],
        loginPsd: [{ validator: validateLoginPsd, trigger: "blur" }]
      },
      // 注册
      registerShow: false, //注册页面的显示
      //登录
      loginShow: false, //登录页面弹窗显示
      checkedTrue: true, //记住账号
      //用户数据
      personInfo: false,
      // service:""
      passwdModel:false,
      passwdModelvalue:null,
      passwdModelSendMes:null,
      qdText:'',//签到描述
      showQd: false,
      qdLoading: false,
      moneyBal: 0,
      qdMoney:0,
      closeQd: false,

      btnLoading: false, //按钮loadding
      codeUrl: '', //注册验证码
      isTourist: false,

      botNavList: [], //底部菜单列表
      english: english
    };
  },
  mounted() {
    window.addEventListener('scroll', this.handleScroll); //导航浮动
    var that = this
    this.$http.post(this.urlRequest + "?m=win&c=lobby&a=getRoomList").then(res=>{
      that.lottery_list = res.data.data.room_list
      for(var i=0;i<that.lottery_list.length;i++){
        if(that.lottery_list[i].lotteryId==12){
          that.lottery_list.splice(i, 1)
        }
      }
    })
  },
  watch:{
    registerShow(boolean){ //注册选项是否显示
      if(boolean && !this.OptionalItems.email){
        this.register();
      }
    },
    tipsFunText(number){ //公告弹窗
      if(number>0){
        this.tipsFun();
        // this.$emit("tipsFunFlag",true);
        // this.tipsFunText = false;
      }
    },
    accountData(obj) {
			if (obj.is_tourist && obj.is_tourist != 1) {
        this.isTourist = true;
			} else {
        this.isTourist = false;
      }
		},
    userInfo(obj) {
      if(!_.isEmpty(obj.username)){
        this.closeQd = false
        fetchJson(this, this.urlRequest + '?m=api&c=app&a=getSignCont',{}).then(result=>{
          if(result.status == 0){
            this.closeQd = true
            // return

            //获取签到数据
            this.qdText = result.data.explain
            var qdObj = this.getQdObj
            qdObj.qdCurrent=result.data.count
            qdObj.qdState=result.data.is_sign
            this.moneyBal=result.data.money_bal
            this.qdMoney=result.data.money
            qdObj.qdMoney = this.qdCurrent==6?result.data.money_bal:result.data.money
            this.$store.commit('setQdObj',qdObj)
            if(qdObj.qdState==0&&JSON.parse(localStorage.getItem('isUser'))){
              var vm = this;
              setTimeout(function(){
                vm.showQd=true
              },200)
            }
          }
          
        });
      }
    },
    Optstate(val){ //注册弹窗QQ 微信 手机 邮箱 是否显示必填
      if(val){
        // this.registerrule.qq = [{ validator: validateQQ, trigger: "blur", required: this.Optstate }],
        // this.registerrule.weixin = [{ validator: validateWeixin, trigger: "blur", required: this.Optstate }],
        // this.registerrule.mobile = [{ validator: validateMobile, trigger: "blur", required: this.Optstate }],
        this.registerrule.email = [{ validator: validateEmail, trigger: "blur", required: this.Optstate }],
        this.registerrule.register = [{ validator: validateRegister, trigger: "blur", required: this.Optstate }]
      }
    }
  },
  computed: {
    ...mapGetters(["userInfo", "accountData", "isLogin", 'service','getQdObj','platformConfig'])
  },
  created () {
    this._getService();
    this.wotal();
    this._getplatformConfig();
    this._getBotNavList();
  },
  methods: {
    _getBotNavList() {
      this.$http.post(this.urlRequest + "?m=api&c=app&a=listArticleColumn&type=2").then(res => {
          if (res.data.status == 0) {
            this.botNavList = res.data.data;
          }
      });
    },
    handleScroll(e){ //导航浮动
      if(window.pageYOffset > 102){
        this.isFixedClass = true;
      }else{
        this.isFixedClass = false;
      }
    },
    goRoom(roomLogo,item){
      if (!localStorage.getItem("token")) {
      // if (_.isEmpty(localStorage.getItem("userData"))) {
        this.$Modal.warning({
            content: english.您还未登录+"，"+english.请先登录,
            scrollable: true,
            onOk: () => {
              var vm = this;
              setTimeout(() => {
                vm.$router.push({path:'/login'});
              }, 1000);
            }
        });
        return false;
      }
      if(item.status==1){
        this.$Message.warning(english.该房间已关闭);          
        return
      }
      var lottery_type = item.lottery_type
      if(lottery_type==12){
        this.$Message.info(english.暂未开放+"，"+english.敬请期待)
        return
      }
      var id = item.id
      var pwd = item.passwd
      var maxYb = parseInt(item.max_yb)
      var lowYb = parseInt(item.low_yb)
      var tips = item.lack_tips
      var userMoney = parseInt(this.accountData.money);
      if(pwd != '' && pwd != undefined){
        this.passwdModel = true
        this.passwdModelSendMes = item
        // console.log(this.passwdModelSendMes)
      }else{
        if(maxYb<userMoney || userMoney<lowYb){
          if(_.isEmpty(tips)){
            this.$Message.warning(english.进入失败+"，"+english.请稍后重试);
          }else{
            this.$Message.warning(tips);
          }      
            return false;
        }
        const {href} = this.$router.resolve({
          path:'/room/'+id,
          params: {id: id},
          query: {id: id,lottery_type: lottery_type}
          
        })
        var tempwindow=window.open()
        tempwindow.location= href
      }
    },
    PrivacyRoom(){//私密房间
      let that = this
      fetchJson(this,'?m=api&c=lobby&a=privateRoom',{lottery_type:that.passwdModelSendMes.lottery_type,secret_pwd:that.passwdModelvalue}).then(result=>{
          if(result.status===0){
            if(result.data.passwd===that.passwdModelvalue){
              localStorage.setItem('MdStory',english.私密房间)
              //密码正确
              router.push({
                name:"Room",
                path:'/room/'+result.data.id,
                params: { lottery_type:that.passwdModelSendMes.lottery_type,id:result.data.id},
                query: {id: result.data.id,lottery_type: that.passwdModelSendMes.lottery_type}
              },function(){
                location.reload()
              });
              
            }else{
              this.$Message.warning(english.房间密码错误);              
            }
          }else{
            this.$Message.warning(english.房间密码错误);
          }
        });
    },
    cancel() { //关闭平台公告
      this.popUp=false;
    },
    wotal() {
      this.$http.post(this.urlRequest + "?m=win&c=lobby&a=getHeaderFooter").then(res=>{
        this.SET_HEADER_FOOTER({
              obj: res.data.data
          });
        this.withdrawTotal = (res.data.data.fit_total+'') 
        this.earn_rate =res.data.data.earn_rate
      })
    },
    myFor() { //导航菜单下拉是否显示
      this.myFlag = false;
      this.showDrop = !this.showDrop
    },
    myForShow() { //导航菜单下拉是否显示
      this.myFlag = true;
      this.showDrop = !this.showDrop
    },
    isRecharge() { //点击充值判断是否游客
      if (!JSON.parse(localStorage.getItem("isUser"))) {
        this.$Modal.warning({
            content: english.游客暂无权限+'，'+english.请先注册会员,
            scrollable: true,
            onOk: () => {
              var vm = this;
              setTimeout(() => {
                vm.$router.push({path:'/reg'});
              }, 1000);
            }
        });
      } else {
        this.$router.push({path:'/topUpCenter/recharge'});
      }
    },
    isWithdraw() { //点击提现判断是否游客
      if (!JSON.parse(localStorage.getItem("isUser"))) {
        this.$Modal.warning({
            content: english.游客暂无权限+'，'+english.请先注册会员,
            scrollable: true,
            onOk: () => {
              var vm = this;
              setTimeout(() => {
                vm.$router.push({path:'/reg'});
              }, 1000);
            }
        });
      } else {
        this.$router.push({path:'/topUpCenter/withdraw'});
      }
    },
    _getService() { //获取客服链接
      this.$http.post(this.urlRequest + "?m=api&c=app&a=customerType").then(res => {
        var newData =  res.data.info;
        for(var i=0; i<newData.length; i++){
          if(newData[i].name == "link"){
            this.SET_SERVICE({
              String: newData[i].value
            }); 
          }
        }
     });
    },
    _getplatformConfig() { //平台配置信息
      this.$http.post(this.urlRequest +"?m=api&c=app&a=getPlatformConfig").then(res=>{
        if (res.data.status == 0) {
          this.SET_PLATFORM_CONFIG({
            obj: res.data.data
          });
        } else {
          this.$Message.error(res.data.ret_msg);
        }
      })
    },
    mydemo(loginInfo){ // 游客
      let params = {           
        flag: "4",
        code: "123",  
      };
      this.$http.post(this.urlRequest + "?m=api&c=user&a=registerMachine",params).then(res => {
        if (res.data.status == 0) {
          localStorage.setItem("token", res.data.token);
          localStorage.setItem("isLogin", true);
          localStorage.setItem("isUser", false);

          this.$http.post(this.urlRequest + "?m=api&c=user&a=userInfo", {'token':localStorage.getItem("token")}).then(res => {
            this.SET_USER_INFO_DATA({
              Obj : res.data.data
            })
          })
          // this.SET_USER_INFO_DATA({
          //   Obj : res.data
          // })
          this.SET_ACCOUNT_DATA({
            Obj:{}
          })
          localStorage.setItem("userData", JSON.stringify(res.data));
          loginStore.commit("changeIsLogin", true);
          
          // this.SET_ISLOGIN({
          //   boolean: true
          // });
          this.$Message.success(english.登录成功+"!");
        } else {
          this.$Message.error(res.data.ret_msg);
        }
      }).catch(error => {
        this.$Message.error(english.数据请求超时);
      });
    },
    // Recharge: function () {
    //   this.$router.push({
    //     path: '/topUpCenter'
    //   });
    // },
    // Withdraw(){
    //   this.$router.push({
    //     path: '/topUpCenter/withdraw/present'
    //   });
    // },
    ...mapActions([
      'SET_HEADER_FOOTER',
      'SET_PLATFORM_CONFIG',
      "SET_ISLOGIN",
      "SET_SERVICE",
      'SET_USER_INFO_DATA',
      'SET_ACCOUNT_DATA',
      'SET_USER_DATA'
    ]),
    // jumpToPersonalCenter: function() { //点击用户跳转到个人中心
    //   this.$router.push({
    //     path: "/personalCenter",
    //     name: "PersonalCenter"
    //   });
    // },
    
    register: function() { //注册
      this.registerShow = true;
      this.verificationCode();
      this.loading = true;
      //注册弹窗QQ 微信 手机 邮箱 是否显示必填
      this.$http.post(this.urlRequest + "?m=api&c=trend&a=getRegInfo").then(res => {
        this.loading = false;
        if (res.data.status == 0) {
          this.OptionalItems = res.data.register;
          if(res.data.state == 0){
              this.Optstate = false;
          }else{
              this.Optstate = true;
          }
        }
      });
    },
    registerCancel: function() { //关闭注册弹框
      this.registerInfo.userName = '';
      this.registerInfo.psd = '';
      this.registerInfo.confirmPsd = '';
      this.registerInfo.qq = '';
      this.registerInfo.weixin = '';
      this.registerInfo.mobile = '';
      this.registerInfo.email = '';
      this.registerInfo.register = '';
      this.registerInfo.referrercode = '';
    },
    verificationCode() { //点击刷新验证码
      var timestamp = new Date().getTime();
      this.codeUrl = this.imgRequest + '?m=web&c=user&a=validatecode&time=' + timestamp;
    },
    registerNow(registerInfo) { //立即注册
      this.$refs[registerInfo].validate(valid => {
        if (valid) {
          var params = {
            username: this.registerInfo.userName,
            password: this.registerInfo.psd,
            password2: this.registerInfo.confirmPsd,
            qq: this.registerInfo.qq,
            weixin: this.registerInfo.weixin,
            mobile: this.registerInfo.mobile,
            email: this.registerInfo.email,
            register: this.registerInfo.register,
            flag: "4",
            code: "123",
            type: "1",
            referrer: this.registerInfo.referrercode
          };
          this.btnLoading = true;
          this.$http.post(this.urlRequest + "?m=api&c=user&a=reg", params).then(res => {
            this.btnLoading = false;
            if (res.data.status == 0) {
              localStorage.setItem("token", res.data.token);
              localStorage.setItem("isLogin", true);
              localStorage.setItem("isUser", true);

              this.$http.post(this.urlRequest + "?m=api&c=user&a=userInfo", {'token':localStorage.getItem("token")}).then(res => {
                this.SET_USER_INFO_DATA({
                  Obj : res.data.data
                })
              })

              //注册成功就登录
              localStorage.setItem("userData", JSON.stringify(res.data)); 
              // this.SET_USER_INFO_DATA({
              //   Obj: res.data
              // });
              this.SET_ACCOUNT_DATA({
                Obj: {}
              });
              this.$Message.success(english.注册成功+"!");
              this.registerShow = false;
              
              this.SET_ISLOGIN({
                boolean: true
              });

            } else if (res.data.status == 1713) {
              //校验账号是否存在
              this.$refs.username.focus();
              this.userName = this.registerInfo.userName;
              this.$Message.error(res.data.ret_msg);
              var vm = this;
              setTimeout(() => {
                vm.$refs.username.blur();
              }, 20);
              
            } else if (res.data.status == 100022) {
              //校验推荐人
              this.$refs.referrerCode.focus();
              this.referrer = this.registerInfo.referrercode;
              this.$Message.error(res.data.ret_msg);
              var vm = this;
              setTimeout(() => {
                vm.$refs.referrerCode.blur();
              }, 20);
            } else {
              this.$Message.error(res.data.ret_msg);
            }
          }).catch(error => {
            this.btnLoading = false;
            this.$Message.error(english.数据请求超时);
          });
        }
      });
    },
    closeRegisterBtn: function() { //关闭注册页面
      this.registerShow = false; //注册页面的显示
    },
    login: function() { //跳出登录页面
      this.loginShow = true;
      if(localStorage.getItem("password")){
        this.rememberPsd = true;
      }else{
        this.rememberPsd = false;
      }
    },
    notOpen: function(){
      this.$Message.info(english.暂未开放+'，'+english.敬请期待QAQ)
    },
    loginNow(loginInfo) { //立即登录
      this.$refs[loginInfo].validate(valid => {
        if (valid) {
          var params = {
            username: this.loginInfo.loginUserName,
            password: this.loginInfo.loginPsd,
            flag: "4",
            code: "1234",
            type: "1",
            openid: "",
            nickname: "",
            avatar: ""
          };
          this.btnLoading = true;
          this.$http.post(this.urlRequest + "?m=api&c=user&a=login", params).then(res => {
            this.btnLoading = false;
            if (res.data.status == 0) {
              this.loginShow = false;
              localStorage.setItem("token", res.data.token);
              localStorage.setItem("isLogin", true);
              localStorage.setItem("isUser", true);
              this.$http.post(this.urlRequest + "?m=api&c=user&a=userInfo", {'token':localStorage.getItem("token")}).then(res => {
                this.SET_USER_INFO_DATA({
                  Obj : res.data.data
                })
              })

              //旧的登录状态
              localStorage.setItem("userData", JSON.stringify(res.data));
              // this.SET_USER_DATA({
              //   obj: res.data
              // });
              this.SET_ACCOUNT_DATA({
                Obj: {}
              });
              loginStore.commit("changeIsLogin", true);
              this.SET_ISLOGIN({
                boolean: true
              });
              this.$Message.success(english.登录成功+"!");
              this.$emit("tipsFunFlag", 1); //平台公告
              this.SET_USER_INFO_DATA({
                Obj : res.data
              })
              this.tipsFun(); //平台公告
              if (this.rememberPsd) {
                localStorage.setItem("userName",this.loginInfo.loginUserName),
                localStorage.setItem("password", this.loginInfo.loginPsd);
              }else{
                localStorage.setItem("password","");
                this.loginInfo.loginPsd = ""
              }
            } else {
              this.$Message.error(english[res.data.ret_msg]);
            }
          }).catch(error => {
            this.btnLoading = false;
            this.$Message.error(english.数据请求超时);
          });
        } else {
          this.$Message.error(english.登录失败+"!");
        }
      });
    },
    tipsFun() { //平台公告
      this.$http.post(this.urlRequest + "?m=win&c=lobby&a=index").then(res=>{
        this.id = res.data.data.popup_msg_id
        this.$http.post('?m=api&c=message&a=detailNoneToken',{id:this.id}).then(res=>{
          if (res.data.list) {
            this.content = res.data.list.content
            this.title = res.data.list.title
            this.addtime = res.data.list.addtime
            this.addtime = formatDate(new Date( this.addtime* 1000), 'yyyy-MM-dd hh:mm') 
            if (res.data.list.is_popup == 1) {
             this.popUp = true;
            }
          }
        })
      })
    },
    exitLogin: function() { //退出登录
      var params = {
        token: localStorage.getItem("token")
      };
      this.$http.post(this.urlRequest + "?m=api&c=user&a=logout", params).then(res => {
        localStorage.removeItem("token");
        this.$Message.success("Logout successfully！");
        this.SET_USER_INFO_DATA({
          Obj: {}
        });
        this.SET_ACCOUNT_DATA({
          Obj: {}
        })
        this.SET_USER_DATA({
          obj: {}
        });
        this.$router.push({ //跳转到首页
          path: "/home",
          name: "Home"
        });
      });
    },
    closeLoginBtn: function() { //关闭登录页面
      this.loginShow = false; //登录页面弹窗显示
    },
    jumpToRegister: function() { //登录页面跳转到注册页面
      this.loginShow = false;
      this.registerShow = true;
    },
    jumpToLogin: function() { //注册页面跳转到登录页面
      this.registerShow = false;
      this.loginShow = true;
    },
    skipHome(){ //跳转到首页
      this.$router.push({path:'/home'});
    },
    goZouShi: function(){
      window.open(
        this.imgRequest +
          "/pczs/Trendssc/trendHeader.html?trendType=basic&lotteryId=9"
      );
    },
    setQd: function(){ //签到
      if(this.getQdObj.qdState!=0){
        return
      }
      if(!JSON.parse(localStorage.getItem('isUser'))){
        this.$Message.warning(english.游客暂无权限+'，'+english.请先注册会员)
        return
      }
      if(this.closeQd){
        this.$router.push({
          path: '/taskCenter'
        })
      }
      this.qdLoading=true
      fetchJson(this,'?m=api&c=app&a=taskSign',{type: 7}).then(result=>{
        if(result.status==0){
          var qdObj = this.getQdObj
          qdObj.qdState = 1
          qdObj.qdCurrent +=1
          this.$store.commit('setQdObj',qdObj)
          this.$Modal.success({
            content: english.签到成功+'，'+english.恭喜您获得+this.getQdObj.qdMoney+english.元宝+'！'
          })
          var accountData = this.accountData
          accountData.money_usable=parseFloat(accountData.money_usable)
          accountData.money_usable+=parseInt(this.getQdObj.qdMoney)
          this.SET_ACCOUNT_DATA({
              Obj: accountData
          })
        }else if(result.status==-1){
          this.$Modal.warning({
            content: result.ret_msg
          })
        }
        this.qdLoading=false
      })
    }
  },
  
};
</script>
