import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
const loginStore = new Vuex.Store({
    state: {
        isLogin: false,
        userData: {},
        userAccountData: {},
        userInfo: {},
        is_banded_bank: '0',
        is_set_paypassword: '0',
        awai:false,
        is_demo:""
    },
    mutations: {
        changeIsLogin(state, msg) {
            state.isLogin = msg
        },
        getUserData(state, msg) {
            state.userData = msg;
        },
        getUserAccountData(state, msg) {
            state.userAccountData = msg
        },
        getUserInfo(state, msg) {
            state.userInfo = msg
        },
        getUserBnaked(state, msg) {
            state.is_banded_bank = msg
        },
        getUserSetPsd(state, msg) {
            state.is_set_paypassword = msg
        },
        getawai(state,msg){
            state.awai =msg
        },
        isUser(state,val){
            state.is_demo = val
        }
    },

})
export default loginStore;


