export default {
    headerShow(state){
        return state.headerShow
    },
    footerShow(state){
        return state.footerShow
    },
    isLogin(state) {
        return state.isLogin;
    },
   
    money_freeze(state) {
        return state.money_freeze;
    },

    service(state){
        return state.service
    },
    stopBet(state){
        return state.stopBet
    },
    getHeaderFooter(state){
        return state.getHeaderFooter
    },
    platformConfig(state){
        return state.platformConfig
    },
    bankedData(state){
        return state.bankedData
    },
    

    accountData(state) {
        return state.accountData;   
    },
    userInfo(state){
        return state.userInfo
    },
    showMiPai(state){
        return state.showMiPai
    },
    getEmail(state){
        return state.myEmail = state.userInfo.email
    },
    showAskRoad(state){
        return state.showAskRoad
    },
    setId(state){
        return state.id
    },
    getQdObj(state){
        return state.qdObj
    },
    lotRstData(state) {
        return state.lotRstData
    },
    serTime(state) {
        return state.serTime
    },
    newRstData(state) {
        return state.newRstData
    },
}


