<template>
  <div>
  <!-- footer -->
  <div class="footer">
    <div class="w-1200">
      <div class="bottom-nav fl">
        <div class="footer-nav" v-if="subNavData.length">
          <router-link :to="{path:'/article/page',query:{type: item.type}}" v-for="(item,index) in subNavData" :key="index">{{english[item.name]}}</router-link>
        </div>
        <p>© 2011-2020 <strong class="platform">{{platformConfig.platform_name}}</strong> {{english.版权所有}}
          <!--<span>菲律宾政府PAGCOR博彩牌照认证</span>--></p>
        <p><strong class="platform">{{platformConfig.platform_name}}</strong> {{english.郑重提示}}</p>
      </div>
      <div class="authorized fr">
        <p>{{english.权威认证资质}}</p>
        <div class="item-box">
        </div>
      </div>
    </div>
  </div>
  </div>
</template>
<script>
// import Scroll from '@/base/scroll/scroll'
import { mapGetters, mapActions } from "vuex";
import english from '@/assets/json/English.json'
export default {
  data() {
    return {
      reg_total:"",
      //关于我们链接
      subNavData:[],
      english: english
    };
  },
  created() {
    //获取关于我们菜单数据
    this.$http.post(this.urlRequest + "?m=api&c=app&a=listArticleColumn&type=1").then(res => {
      if (res.data.status == 0) {
        this.subNavData = res.data.data;
      } else {
        this.$Message.error(res.data.ret_msg);
      }
    });
  },
  mounted() {

  },
  computed: {
    ...mapGetters(["getHeaderFooter", 'platformConfig'])
  },
  methods: {
    
  },
  components: {},
  watch: {

  },
  deactivated() {},
  beforeDestroy() {}
};
</script>

<style scoped>

</style>



